import { Component, NgZone, OnInit } from '@angular/core';
import { ApiservicesService } from '../../provider/apiservices.service';
import { Router } from "@angular/router";
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { MenuController } from '@ionic/angular';
import { DataTransferService } from '../../services/data-transfer.service';
import { Location } from '@angular/common';
import { EventsService } from 'src/app/services/events.service';

@Component({
  selector: 'app-question-score-card',
  templateUrl: './question-score-card.page.html',
  styleUrls: ['./question-score-card.page.scss'],
})

export class QuestionScoreCardPage implements OnInit{

  policyData:any;
  hrPolicyDetailData:any;
  submitQuizData:any;
  progressPercentage:number=0;
  commingData:any;

  constructor(private apiservices:ApiservicesService,private router:Router,private zone:NgZone,private eventService:EventsService,
    public menuCtrl:MenuController,private sendData:DataTransferService,private location:Location) { 

    this.policyData=this.sendData.alldata;
    console.log("policy data==", this.policyData);
    this.hrPolicyDetailData=this.sendData.hrPolicyDetailAllData;
    console.log("hr Policy Detail Array Redirection==", this.hrPolicyDetailData);
    this.submitQuizData=this.sendData.quizData;
    console.log("submit Quiz then API response all Data==", this.submitQuizData);
    // this.submitQuizData.min_score=this.submitQuizData.scorecard.base_score/this.submitQuizData.scorecard.score;
    this.progressPercentage=this.submitQuizData.data.scorecard.score/this.submitQuizData.data.scorecard.base_score*100;
    console.log("get percentage==", this.progressPercentage);
    this.commingData=this.sendData.commingPageName;
    console.log("comming from which page==", this.commingData);

  }









  ngOnInit(){}

  hardWareBackButton(){
    this.zone.run(()=>{
      this.apiservices.hrPolicyCount=this.apiservices.hrPolicyCount-1;
      if(this.commingData.commingFrom=="hrPolicyList"){
        let data={
          "commingFrom":"quizScoreCard",
          "reloadPage":true
        }
        this.eventService.publishSomeData({data});
        this.apiservices.hrPolicyCount=0;
        this.router.navigate(["/hr-policy-detail"]);
      }else{
        let data={
          "commingFrom":"reloadHomeApi",
          "reloadHomePage":true
        }
        this.eventService.publishSomeData({data});
        this.apiservices.hrPolicyCount=0;
        this.router.navigate(["/tabs/tab1"]);
      }
    });
  }

  goToNext(value){
    this.zone.run(()=>{
      if(value=="retake"){
        var data={
          "commingFrom":"retakeQuiz",
          "reloadPage":true
        }
        this.eventService.publishSomeData({data});
        this.apiservices.hrPolicyCount=this.apiservices.hrPolicyCount-1;
        this.location.back();
      }else{
        let apiKey={
          "hrpolicy_id":this.policyData.hrpolicy_id
        };
        console.log("Quiz Question List api keys==", apiKey);
        this.apiservices.apiCallWithLoginToken(URLS.hrPolicyRetakeQuiz, apiKey).subscribe((result) => {
          console.log("Quiz Question List Api Result==", result);
          if (result.status == 1 || result.status == '1') {  
            this.apiservices.hrPolicyCount=0;
            this.router.navigate(["/hr-policy-detail"]);
          }else{
            this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          }
        }, err=>{
          this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
        });
      }
    })
  }

  successButton(){
    // this.apiservices.hrPolicyCount=this.apiservices.hrPolicyCount+1;
    if(this.hrPolicyDetailData.length>0){
      if(this.apiservices.hrPolicyCount==this.hrPolicyDetailData.length){
        var data={
          "commingFrom":"reloadHomeApi",
          "reloadHomePage":true
        }
        this.eventService.publishSomeData({data});
        if(this.commingData.commingFrom=="hrPolicyList"){
          this.router.navigate(["/hr-policy-list"]);
        }else{
          this.router.navigate(["/tabs/tab1"]);
        }
        // this.router.navigate(["/hr-policy-list"]);
        this.policyData.action="0";
      }else{
        this.sendData.alldata=this.policyData;
        this.sendData.myParam=this.hrPolicyDetailData[this.apiservices.hrPolicyCount];
        this.sendData.hrPolicyDetailAllData=this.hrPolicyDetailData;
        this.sendData.commingPageName=this.commingData;      
        this.router.navigate([this.hrPolicyDetailData[this.apiservices.hrPolicyCount].data.redirect_page]);
      }
    }
  }








  
}